.mdl-container{position:absolute;top:0;left:0;height:100%;width:100%}.mdl-container .mdl-overlay{position:fixed;padding:0;margin:0;height:100%;width:100%;z-index:1;background:#373a47}.mdl-container .mdl{position:absolute;top:50%;left:50%;-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%);padding:.5rem;width:45%;border-radius:3px;background:#fff;z-index:2;overflow:auto}@media only screen and (max-width:1366px){.mdl-container .mdl{width:80%}}@media only screen and (max-height:1229px){.mdl-container .mdl{height:45%}}